/* ブレークポイント
----------------------------------------- */
$breakpoints: (
  'sm': 'screen and (min-width: 640px)',
  'md': 'screen and (min-width: 768px)',
  'lg': 'screen and (min-width: 1024px)',
  'xl': 'screen and (min-width: 1280px)',
) !default;

$sp: 320px;
$sm: 640px;
$md: 768px;
$lg: 1024px;
